import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
// import appList from './appList.vue'
import pageView from '@/components/common-ui/page-view.vue';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        this.selectedLabelDefault = 'appList';
        this.tabs = [{
                label: '系统配置',
                icon: 'icon_yingyong',
                value: 'appList'
            }, {
                label: '成员管理',
                icon: 'icon_gongzhonghao',
                value: 'agentConfig'
            }, {
                label: '订单',
                icon: 'iconzhangdanguanli',
                value: 'order'
            }];
    }
    clickHandler(value) {
        // if you clicked home tab, then print 'Home'
        console.log(value);
    }
    changeHandler(label) {
        // if you clicked different tab, this methods can be emitted
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    mounted() {
        window.document.title = this.schoolInfo.name;
    }
};
Home = __decorate([
    Component({
        components: {
            appList: () => import('./appList.vue'),
            pageView,
            agentConfig: () => import('./agentConfig/index.vue'),
            order: () => import('./orderConfig/index.vue')
        }
    })
], Home);
export default Home;
